import React, { useState } from 'react';
import { Button, Drawer, List, ListItem, ListItemText, useMediaQuery } from '@mui/material';
import { Link, useLocation } from 'react-router-dom';

const SecondaryNavbar = () => {
  const [openDrawer, setOpenDrawer] = useState(false);
  const isSmallScreen = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const category = params.get('category');

  const toggleDrawer = (open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setOpenDrawer(open);
  };

  return (
    <div style={{ backgroundColor: '#D63F1F', height: '40px', display: 'flex', justifyContent: 'center' }}>
      {isSmallScreen ? (
        <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} onClick={toggleDrawer(true)}>Categoras</Button>
      ) : (
        <>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category">Todo</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=Deporte">Deportes</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=Electrodomsticos">Electrodomsticos</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=Cosmticos">Cosmticos</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=Tecnologa">Tecnologia</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=musica">Musica</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=informatica">Informatica</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=ofertas">Ofertas</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=ultimas-novedades">ltimas Novedades</Button>
          <Button color="inherit" style={{ color: 'white', margin: '0 20px', fontWeight: 'bold' }} component={Link} to="/productos?category=los-mas-vendidos">Los ms vendidos</Button>
        </>
      )}

      <Drawer anchor="top" open={openDrawer} onClose={toggleDrawer(false)}>
        <List style={{ backgroundColor: '#D63F1F', color: 'white', width: '100%' }}>
          <ListItem button component={Link} to="/productos?category" onClick={toggleDrawer(false)}>
            <ListItemText primary="Todo" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=Deporte" onClick={toggleDrawer(false)}>
            <ListItemText primary="Deportes" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=Electrodomsticos" onClick={toggleDrawer(false)}>
            <ListItemText primary="Electrodomsticos" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=Cosmticos" onClick={toggleDrawer(false)}>
            <ListItemText primary="Cosmticos" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=Tecnologa" onClick={toggleDrawer(false)}>
            <ListItemText primary="Tecnologa" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=musica" onClick={toggleDrawer(false)}>
            <ListItemText primary="Msica" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=informatica" onClick={toggleDrawer(false)}>
            <ListItemText primary="Informtica" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=ofertas" onClick={toggleDrawer(false)}>
            <ListItemText primary="Ofertas" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=ultimas-novedades" onClick={toggleDrawer(false)}>
            <ListItemText primary="ltimas Novedades" />
          </ListItem>
          <ListItem button component={Link} to="/productos?category=los-mas-vendidos" onClick={toggleDrawer(false)}>
            <ListItemText primary="Los ms vendidos" />
          </ListItem>
        </List>
      </Drawer>
    </div>
  );
};

export default SecondaryNavbar;
